import React from "react";
import io from 'socket.io-client';
import './App.css';

const socket = io("http://bebe.group:8080");
socket.on("connect", () => {
    console.log("Socket connected.");
});
socket.on("disconnect", () => {
    console.log("Socket disconnect");
});
socket.connect();

export class App extends React.Component {
    constructor(props) {
        super(props);

        // React state
        this.state = {
            frontend_counter: 0,
            backend_counter: 0
        };

        this.updateBackendCounter = this.updateBackendCounter.bind(this);

        socket.on("event_name", this.updateBackendCounter)
    }

    updateBackendCounter(updated_count) {
        this.setState({
            backend_counter: updated_count
        });
    }

    componentDidMount() {
        // Increase count every second
        setInterval(() => {

           // Use react setState to update render state
           this.setState({
              frontend_counter: this.state.frontend_counter + 1
           });

        }, 1000);
    }

    render() {
        // Javascript level logic here
        let title = "Poopity: ";
        let title2 = "Scoopity: ";
        let title3 = "How many scoops could a poop scoopity?";
        let poop1 = this.state.frontend_counter;
        let poop2 = this.state.frontend_counter + 1;
        let scoop = poop1 + poop2;
        let frontend_title = "Frontend Counter: ";
        let backend_title = "Backend Counter: ";

        // JSX (Html) Render logic here
        return (
            <React.Fragment>
            <div>
              <span>
                  <h1>{title}</h1>
                  <em className='red'>{poop1}</em>
              </span>
              <span>
                  <h1>{title2}</h1>
                  <em className='blue'>{poop2}</em>
              </span>
                <span>
                    <h1>{title3}</h1>
                    <em className='red'>{scoop}</em>
                </span>
                <span>
                    <h2>{frontend_title}</h2>
                    {this.state.frontend_counter}
                </span>
                <span>
                    <h1>{backend_title}</h1>
                    {this.state.backend_counter}
                </span>
            </div>
            </React.Fragment>
        )
    }
}
